import React, { useState, useEffect, Fragment } from 'react'
import { useLazyQuery } from '@apollo/react-hooks';

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import PersonDetail from './PersonDetail'
import PersonEdit from './PersonForm'
import { useMutation } from "@apollo/react-hooks"

import { Person_Query } from '../../queries/person.query'
import { Delete_Person } from '../../mutations/Person/person'
import { Restore_Person } from '../../mutations/Person/person'
import { Button } from '@material-ui/core';

import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import SearchIcon from '@material-ui/icons/Search';
import Slide from '@material-ui/core/Slide';
import RestoreIcon from '@material-ui/icons/Restore';
import PersonForm from "./../Person/PersonForm";

import Tooltip from '@material-ui/core/Tooltip'
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import TablePagination from '@material-ui/core/TablePagination';

import { withStyles } from '@material-ui/core/styles';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})



const Person = props => {

    useEffect(() => {
        getPersons({ variables: { start: page, end: rowsPerPage } })
    }, [])
    const [getPersons, { data }] = useLazyQuery(Person_Query, {
        fetchPolicy: 'no-cache',
        onCompleted: () => {
            console.log(data)
            let newPersons = data.getPersons.map(person => {
                let sumRating = 0
                console.log('person.rating', person.rating)
                person.rating.map(rate => {
                    sumRating += rate.rating
                })
                if (person.rating.length > 0) {
                    console.log('sumRating', sumRating)
                    person.rate = sumRating / person.rating.length
                }
                else
                    person.rate = 0

                return person
            })
            setPersons(newPersons)
            setTotalPersons(data.totalPersons)
        }
    })

    const [deletePerson] = useMutation(Delete_Person)
    const [restorePerson] = useMutation(Restore_Person)

    const classes = useStyles()

    const [persons, setPersons] = useState([])
    const [totalPersons, setTotalPersons] = useState(0)
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);


    const [open, setOpen] = useState(false);
    const [openRestorePerson, setOpenRestorePerson] = useState(false);
    const [openDetailPerson, setOpenDetailPerson] = useState(false);
    const [openEditPerson, setOpenEditPerson] = useState(false);
    const [openCreatePerson, setOpenCreatePerson] = useState(false);
    const [person, setPersonSelected] = useState({})


    const handleChangePage = (event, newPage) => {
        setPage(newPage);
        getPersons({ variables: { start: newPage, end: rowsPerPage } })
    }

    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        getPersons({ variables: { start: 0, end: event.target.value } })
    }

    const onOpenDeletePerson = id => () => {
        setPersonSelected(id)
        setOpen(true);
    }

    const onOpenRestorePerson = id => () => {
        setPersonSelected(id)
        setOpenRestorePerson(true);
    }

    const onCloseDeletePerson = () => {
        setOpen(false);
    }

    const onDeletePerson = () => {
        setOpen(false);

        deletePerson({
            variables: { id: person }
        }).then(response => {

            const newPersons = persons.map((currentPerson, index) => {
                if (person !== currentPerson.id)
                    return currentPerson

                currentPerson.status = -1
                return currentPerson
            })
            setPersons(newPersons)
        })
    }

    const onRestorePerson = () => {

        setOpenRestorePerson(false)

        restorePerson({
            variables: { id: person }
        }).then(response => {
            const newPersons = persons.map((currentPerson, index) => {

                if (person !== currentPerson.id)
                    return currentPerson

                currentPerson.status = 1
                return currentPerson
            })
            setPersons(newPersons)
        })
    }

    const onDetailPerson = person => () => {
        setPersonSelected(person)
        setOpenDetailPerson(true)
    }

    const closeApu = () => {
        setOpenDetailPerson(false)
    }

    const onEditPerson = person => () => {
        setPersonSelected(person)
        setOpenEditPerson(true)
    };
    const onCloseEditPerson = () => {
        setOpenEditPerson(false)
    };
    const onCloseRestorePerson = () => {
        setOpenRestorePerson(false)
    };

    const onCreatePerson = () => {
        setOpenCreatePerson(true)
    }
    const onCloseCreatePerson = () => {
        setOpenCreatePerson(false)
    }


    return (
        <Fragment>

            <Paper className={classes.paper}>
                <Fab onClick={onCreatePerson} variant="extended" color="primary" size="small" className={classes.margin}>
                    <AddIcon className={classes.extendedIcon} />
                        Nuevo usuario
                    </Fab>

                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>NOMBRES</TableCell>
                            <TableCell>APELLIDOS</TableCell>
                            <TableCell>TIPO DE DOCUMENTO</TableCell>
                            <TableCell>DOCUMENTO</TableCell>
                            <TableCell>CORREO</TableCell>
                            <TableCell>TELÉFONO</TableCell>
                            <TableCell>CALIFICACION</TableCell>
                            <TableCell>DETALLE</TableCell>
                            <TableCell>EDITAR</TableCell>
                            <TableCell>ESTADO</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            persons.map(item => {

                                return (
                                    <TableRow key={item.id}>
                                        <TableCell>{item.names} </TableCell>
                                        <TableCell>{item.last_name} </TableCell>
                                        <TableCell>{item.documentType ? item.documentType.documentType : ''}</TableCell>
                                        <TableCell>{item.documentNumber}</TableCell>
                                        <TableCell>{item.email}</TableCell>
                                        <TableCell>{item.phoneNumber}</TableCell>
                                        <TableCell>{item.rate}</TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Detalle" placement="top">
                                                <SearchIcon className={classes.icon} onClick={onDetailPerson(item)} />
                                            </Tooltip>
                                        </TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Editar" placement="top">
                                                <EditIcon className={classes.icon} onClick={onEditPerson(item)} />
                                            </Tooltip>
                                        </TableCell>
                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Eliminar" placement="top">
                                                {
                                                    item.status === 1 ?
                                                        <DeleteIcon className={classes.icon} onClick={onOpenDeletePerson(item.id)} />
                                                        :
                                                        <RestoreIcon className={classes.icon} onClick={onOpenRestorePerson(item.id)} />
                                                }
                                            </Tooltip>
                                        </TableCell>
                                    </TableRow>)
                            }
                            )}
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalPersons}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>


            <Dialog
                open={open}
                onClose={onCloseDeletePerson}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
            </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de eliminar esta persona?
                </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeletePerson} color="primary">
                        NO
            </Button>
                    <Button onClick={onDeletePerson} color="primary">
                        SI
            </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openRestorePerson}
                onClose={onCloseRestorePerson}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de Reactivar a esta persona?
                </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseRestorePerson} color="primary">
                        NO
                    </Button>
                    <Button onClick={onRestorePerson} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog open={openDetailPerson} onClose={closeApu} TransitionComponent={Transition}>
                <DialogContent>
                    <PersonDetail person={person} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeApu} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openEditPerson} onClose={onCloseEditPerson} TransitionComponent={Transition}>
                <DialogContent>
                    <PersonForm onCloseEditPerson={onCloseEditPerson} person={person} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseEditPerson} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                fullWidth={true}
                maxWidth="md"
                open={openCreatePerson}>
                <DialogTitle onClose={onCloseCreatePerson}>Crear usuario</DialogTitle>

                <DialogContent>
                    <PersonForm handleClose={onCloseCreatePerson} modal={true} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={onCloseCreatePerson} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>
        </Fragment>)
}

export default Person