import gql from 'graphql-tag'

export const Customer_Query = gql`{
    getPersons {
        id 
        names
        businessName
    }
  }`


export const Person_Current = gql`
    query getPersonCurrent {
        getPersonCurrent {
            username
        }
    }
  `


export const Person_Query = gql`
query getPersons($start: Int, $end: Int) {
    getPersons(start: $start, end: $end) {
      id
      names
      last_name
      documentNumber
      dateOfBirth
      email
      phoneNumber
      username
      rol
      status
      rating {
        id
        rating
      }
      documentType {
        id
        documentType
      }
      regimeType {
        id
        regimeType
      }
      personType {
        id
        personType
      }
    },
    totalPersons
}
`