import gql from 'graphql-tag'

export const Roles_Query = gql`
   query getRoles($filters: FilterRole ) {
        getRoles(filters: $filters) {
            id
            name 
            status
        }
    }
`

export const Customer_Query = gql`{
    getPersons {
        id 
        names
        businessName
    }
  }`


export const Person_Current = gql`
    query getPersonCurrent {
        getPersonCurrent {
            username
        }
    }
  `


export const Paginate_Roles = gql`
    query getRoles($start:Int, $end:Int) {
        getRoles(start: $start, end: $end) {
            id
            name
            status
            administrative
        }
    }
  `